import { useRef } from "react";
import { Canvas, useFrame } from "@react-three/fiber";
import { Sphere } from "@react-three/drei";
import * as THREE from "three";

import sunTexture from "../models/texture/sun.jpg";
const textureLoader = new THREE.TextureLoader();

const Sun = () => {
  const groupRef = useRef();
  useFrame(() => {
    // groupRef.current.rotation.y += 0.02;
  });
  return (
    <group ref={groupRef}>
      <Sphere args={[2, 16, 32]} position={[0, 0.0, 0]}>
        <meshStandardMaterial attach="material" color={"#f30"} />
      </Sphere>
    </group>
  );
};

const Clouds = () => {
  const groupRef = useRef();
  useFrame(() => {
    // groupRef.current.rotation.y += 0.02;
  });
  return (
    <group ref={groupRef}>
      <Sphere args={[1.25, 16, 32, 0, Math.PI * 2, 0, Math.PI * 0.7]} position={[-1.8, -0.4, 0]}>
        <meshStandardMaterial attach="material" color={"#fff"} />
      </Sphere>
      <Sphere args={[0.75, 16, 32, 0, Math.PI * 2, 0, Math.PI * 0.7]} position={[-0.9, -0.7, 1]}>
        <meshStandardMaterial attach="material" color={"#fff"} />
      </Sphere>
      <Sphere args={[2, 16, 32, 0, Math.PI * 2, 0, Math.PI * 0.7]} position={[1, 0.1, 0]}>
        <meshStandardMaterial attach="material" color={"#fff"} />
      </Sphere>
    </group>
  );
};

function WeatherModel({ skyState }) {
  return (
    <Canvas>
      {/* 빛 */}
      <ambientLight intensity={0.5} />
      <pointLight position={[-10, 10, 10]} intensity={1000} />
      {/* 구름 */}
      {skyState === "sun" ? <Sun /> : <Clouds />}
    </Canvas>
  );
}

export default WeatherModel;
