import { useState, useEffect, useRef } from "react";
import DataJson from "../elements/cityData.json";
const Header = ({ setCurIndex, type, setType, coord, setCoord }) => {
  // 검색
  const inputArea = useRef();

  // 섭씨, 화씨 토글
  const handleToggle = () => {
    type === "shortly" ? setType("weekly") : setType("shortly");
    setCurIndex(0);
  };

  return (
    <header>
      <div className="typeToggle">
        <button className={type === "shortly" ? "active" : ""} onClick={handleToggle}>
          <span>시간별 단기 예보</span>
          <span>주간 기상 예보</span>
        </button>
      </div>
      <div className="cities">
        {[
          { name: "서울특별시", lat: 37, lon: 126 },
          { name: "부산광역시", lat: 35, lon: 129 },
          { name: "인천광역시", lat: 37, lon: 126 },
          { name: "대구광역시", lat: 35, lon: 128 },
          { name: "대전광역시", lat: 36, lon: 127 },
          { name: "광주광역시", lat: 35, lon: 126 },
          { name: "울산광역시", lat: 35, lon: 129 },
          { name: "제주특별자치도", lat: 33, lon: 126 },
        ].map((city) => (
          <button key={city.name} onClick={() => setCoord({ lat: city.lat, lon: city.lon, name: city.name })}>
            {city.name.slice(0, 2)}
          </button>
        ))}
      </div>
      <p className="city">{coord.name}</p>
    </header>
  );
};

export default Header;
