import { useState, useEffect } from "react";

const apiKey = process.env.REACT_APP_API_KEY;
const baseUrl = "https://apis.data.go.kr/1360000/VilageFcstInfoService_2.0/getVilageFcst";

const WeatherService = ({ coord, setLoading }) => {
  const [weatherData, setWeatherData] = useState({});
  // Params
  const [baseTime, setBaseTime] = useState();
  const today = new Date().toISOString().split("T")[0].replace(/-/g, "");

  /* API Fetch */
  const fetchData = async () => {
    setLoading(true);
    try {
      const response = await fetch(
        `${baseUrl}?serviceKey=${apiKey}&pageNo=1&numOfRows=1000&dataType=JSON&base_date=${today}&base_time=${baseTime}&nx=${coord.lat}&ny=${coord.lon}`
      );
      const json = await response.json();
      setWeatherData(json.response.body.items.item.map(filterData).filter(Boolean));
    } catch (error) {
      console.error("Error:", error);
    }
  };
  // API Fetch > 필터링: baseDate, baseTime, nx, ny 키값 제거, category가 UUU,VVV, WAV, VEC 인 키값 쌍 제거
  const filterData = (item) => {
    if (item.category === "UUU" || item.category === "VVV" || item.category === "WAV" || item.category === "VEC") {
      return null;
    }
    const { baseDate, baseTime, nx, ny, ...rest } = item;
    return rest;
  };
  // API Fetch > 불러오기
  useEffect(() => {
    if (baseTime) {
      fetchData();
    }
  }, [baseTime, coord]);

  // Params > baseTime: 현재 시간에 따라 기준시 설정
  const hours = new Date().getHours();
  const formatHour = (hour) => {
    return String(hour).padStart(2, "0") + "00";
  };
  useEffect(() => {
    if (hours === 0 || hours === 1) {
      setBaseTime(formatHour(23));
    } else if (hours % 3 === 1) {
      setBaseTime(formatHour(hours - 2));
    } else if (hours % 3 === 2) {
      setBaseTime(formatHour(hours));
    } else {
      setBaseTime(formatHour(hours - 1));
    }
  }, [hours]);

  return { weatherData };
};

export default WeatherService;
