import { useState, useEffect } from "react";
import ReactDOM from "react-dom/client";

import WeatherService from "./elements/weatherService";
import Header from "./components/Header";
import WeatherModel from "./components/WeatherModel";

import "./scss/common.scss";

const root = ReactDOM.createRoot(document.getElementById("root"));

function App() {
  const [loading, setLoading] = useState(true);
  const [type, setType] = useState("shortly");
  const [coord, setCoord] = useState({ lat: 37, lon: 126, name: "서울특별시" });
  const { weatherData } = WeatherService({ coord, setLoading });

  const [weeklyForecast, setWeeklyForecast] = useState([]);
  const [shortlyForecast, setShortlyForecast] = useState([]);
  const [curIndex, setCurIndex] = useState(0);
  const groupByDateAndTime = (data) => {
    const groupedData = {};

    data.forEach(({ fcstDate, fcstTime, category, fcstValue }) => {
      const ampm = parseInt(fcstTime) < 1200 ? "오전" : "오후";
      const key = `${fcstDate}${ampm}`;

      groupedData[key] ||= { date: fcstDate, ampm, timeline: [] };

      const timeItem = groupedData[key].timeline.find((time) => time.fcstTime === fcstTime);
      timeItem
        ? timeItem.values.push({ category, fcstValue })
        : groupedData[key].timeline.push({ fcstTime, values: [{ category, fcstValue }] });
    });

    return Object.values(groupedData).map(({ date, ampm, timeline }) => ({
      date,
      ampm,
      timeline,
    }));
  };
  const getAverage = (timeline, category) => {
    const values = timeline.flatMap((time) =>
      time.values.filter((item) => item.category === category).map((value) => parseFloat(value.fcstValue))
    );
    return calculateAverage(values);
  };
  const groupByTime = (data) => {
    const groupedData = {};
    data.forEach(({ fcstDate, fcstTime, category, fcstValue }) => {
      const key = `${fcstDate}-${fcstTime}`;
      groupedData[key] ||= { date: fcstDate, time: fcstTime, values: [] };
      groupedData[key].values.push({ category, fcstValue });
    });
    return Object.values(groupedData)
      .slice(0, 6)
      .map(({ date, time, values }) => ({
        date,
        time,
        values,
      }));
  };
  const getValue = (values, category) => {
    const categoryValue = values.find((item) => item.category === category);
    return categoryValue ? parseFloat(categoryValue.fcstValue) : undefined;
  };

  const calculateAverage = (arr) => {
    if (arr.length === 0) return 0;
    const sum = arr.reduce((acc, num) => acc + num, 0);
    return Math.round((sum / arr.length) * 10) / 10;
  };
  useEffect(() => {
    if (weatherData.length) {
      setWeeklyForecast(groupByDateAndTime(weatherData));
      setShortlyForecast(groupByTime(weatherData));
      console.log(groupByDateAndTime(weatherData));
      console.log(groupByTime(weatherData));
      setLoading(false);
    }
  }, [weatherData]);
  useEffect(() => {
    const intervalId = setInterval(() => {
      setCurIndex((prev) => (prev + 1) % 6);
    }, 5000);
    return () => clearInterval(intervalId);
  }, []);

  const ptyFormat = (pty) => {
    switch (String(pty)) {
      case "0":
        return "none";
      case "1":
        return "rain";
      case "2":
        return "rain";
      case "3":
        return "snow";
      case "4":
        return "rain";
    }
  };
  const skyFormat = (sky) => {
    switch (String(sky)) {
      case "1":
        return "sun";
      case "3":
        return "cloud";
      case "4":
        return "clouds";
    }
  };
  const popFormat = (pop) => {
    const parserPop = parseInt(pop);
    return `${parserPop}%`;
  };
  const pcpFormat = (pcp) => {
    const parsedPcp = parseInt(pcp);
    switch (true) {
      case parsedPcp < 1.0:
        return "1.0mm 미만";
      case parsedPcp >= 1.0 && parsedPcp < 30.0:
        return "1.0 ~ 29.0mm";
      case parsedPcp >= 30.0 && parsedPcp < 50.0:
        return "30.0 ~ 50.0mm";
      case parsedPcp >= 50.0:
        return "50.0mm 이상";
      default:
        return "0mm";
    }
  };
  const snowFormat = (snow) => {
    const parsedSnow = parseInt(snow);
    switch (true) {
      case parsedSnow > 0:
        return `(${parsedSnow}cm)`;
      default:
        return "";
    }
  };
  const dateFormat = (date, type = "short") => {
    return new Date(`${date.slice(0, 4)}-${date.slice(4, 6)}-${date.slice(6)}`).toLocaleDateString("ko-KR", {
      weekday: type,
    });
  };

  const [shortlySkyState, setShortlySkyState] = useState([]);
  const [weeklySkyState, seWeeklySkyState] = useState([]);
  useEffect(() => {
    if (weeklyForecast.length && shortlyForecast.length) {
      if (type === "shortly") {
        for (let i = 0; i < shortlyForecast.length; i++) {
          ptyFormat(getValue(shortlyForecast[i].values, "PTY")) !== "none"
            ? setShortlySkyState((prev) => [...prev, ptyFormat(getValue(shortlyForecast[i].values, "PTY"))])
            : setShortlySkyState((prev) => [...prev, skyFormat(getValue(shortlyForecast[i].values, "SKY"))]);
        }
      } else {
        for (let i = 0; i < weeklyForecast.length; i++) {
          ptyFormat(getValue(weeklyForecast[i].timeline[0].values, "PTY")) !== "none"
            ? seWeeklySkyState((prev) => [...prev, ptyFormat(getValue(weeklyForecast[i].timeline[0].values, "PTY"))])
            : seWeeklySkyState((prev) => [...prev, skyFormat(getValue(weeklyForecast[i].timeline[0].values, "SKY"))]);
        }
      }
    }
  }, [type, shortlyForecast, weeklyForecast]);

  useEffect(() => {
    const currentSkyState = type === "shortly" ? shortlySkyState[curIndex] : weeklySkyState[curIndex];
    document.body.className = `${currentSkyState}`;
  }, [curIndex, shortlySkyState, weeklySkyState, type]);

  // TMP	1시간 기온	℃
  // WSD	풍속	m/s
  // SKY	하늘상태	맑음(1), 구름많음(3), 흐림(4)
  // PTY	강수형태	없음(0), 비(1), 비/눈(2), 눈(3), 소나기(4)
  // POP	강수확률	%
  // PCP	1시간 강수량	범주 (1 mm)
  // REH	습도	%
  // SNO	1시간 신적설	범주(1 cm)

  const dummyData = [
    {
      date: "20240104",
      time: "1500",
      values: [
        { category: "TMP", fcstValue: "6" },
        { category: "WSD", fcstValue: "5.7" },
        { category: "SKY", fcstValue: "1" },
        { category: "PTY", fcstValue: "0" },
        { category: "POP", fcstValue: "80" },
        { category: "PCP", fcstValue: "80" },
        { category: "REH", fcstValue: "20" },
        { category: "SNO", fcstValue: "0" },
      ],
    },
    {
      date: "20240104",
      time: "1600",
      values: [
        { category: "TMP", fcstValue: "6" },
        { category: "WSD", fcstValue: "5.7" },
        { category: "SKY", fcstValue: "3" },
        { category: "PTY", fcstValue: "0" },
        { category: "POP", fcstValue: "20" },
        { category: "PCP", fcstValue: "강수없음" },
        { category: "REH", fcstValue: "75" },
        { category: "SNO", fcstValue: "적설없음" },
      ],
    },
    {
      date: "20240104",
      time: "1700",
      values: [
        { category: "TMP", fcstValue: "6" },
        { category: "WSD", fcstValue: "5.7" },
        { category: "SKY", fcstValue: "4" },
        { category: "PTY", fcstValue: "0" },
        { category: "POP", fcstValue: "20" },
        { category: "PCP", fcstValue: "강수없음" },
        { category: "REH", fcstValue: "75" },
        { category: "SNO", fcstValue: "적설없음" },
      ],
    },
    {
      date: "20240104",
      time: "1800",
      values: [
        { category: "TMP", fcstValue: "6" },
        { category: "WSD", fcstValue: "5.7" },
        { category: "SKY", fcstValue: "1" },
        { category: "PTY", fcstValue: "1" },
        { category: "POP", fcstValue: "20" },
        { category: "PCP", fcstValue: "강수없음" },
        { category: "REH", fcstValue: "75" },
        { category: "SNO", fcstValue: "적설없음" },
      ],
    },
    {
      date: "20240104",
      time: "1900",
      values: [
        { category: "TMP", fcstValue: "6" },
        { category: "WSD", fcstValue: "5.7" },
        { category: "SKY", fcstValue: "1" },
        { category: "PTY", fcstValue: "2" },
        { category: "POP", fcstValue: "20" },
        { category: "PCP", fcstValue: "강수없음" },
        { category: "REH", fcstValue: "75" },
        { category: "SNO", fcstValue: "적설없음" },
      ],
    },
    {
      date: "20240104",
      time: "2000",
      values: [
        { category: "TMP", fcstValue: "6" },
        { category: "WSD", fcstValue: "5.7" },
        { category: "SKY", fcstValue: "1" },
        { category: "PTY", fcstValue: "3" },
        { category: "POP", fcstValue: "20" },
        { category: "PCP", fcstValue: "강수없음" },
        { category: "REH", fcstValue: "75" },
        { category: "SNO", fcstValue: "1" },
      ],
    },
  ];

  return (
    <>
      <Header setCurIndex={setCurIndex} type={type} setType={setType} coord={coord} setCoord={setCoord} />
      {loading ? (
        <div className="loading"></div>
      ) : (
        <>
          <section className="visual">
            <ul style={{ left: -curIndex * 100 + "%" }}>
              {type === "shortly"
                ? shortlyForecast.map((fcst, i) => (
                    <li key={i}>
                      <p className="temp">
                        {getValue(fcst.values, "TMP")}
                        <span>&deg;</span>
                      </p>
                      <div>
                        <WeatherModel skyState={shortlySkyState[i]} />
                      </div>
                      <p className="time">{`${fcst.date.slice(4, 6)}/${fcst.date.slice(6)} ${fcst.time.slice(
                        0,
                        2
                      )}시`}</p>
                    </li>
                  ))
                : weeklyForecast.map((fcst, i) => (
                    <li key={i}>
                      <p className="temp">
                        {getAverage(fcst.timeline, "TMP")}
                        <span>&deg;</span>
                      </p>
                      <div>
                        <WeatherModel skyState={weeklySkyState[i]} />
                      </div>
                      <p className="time">{`${dateFormat(fcst.date, "long")} ${fcst.ampm}`}</p>
                    </li>
                  ))}
            </ul>
          </section>
          <section className="detail">
            <ul>
              <li>
                <i className="icon-wind"></i>
                <p>풍속</p>
                <p>
                  {type === "shortly"
                    ? getValue(shortlyForecast[curIndex].values, "WSD")
                    : getAverage(weeklyForecast[curIndex].timeline, "WSD")}
                  m/s
                </p>
              </li>
              <li>
                <i className="icon-humid"></i>
                <p>습도</p>
                <p>
                  {type === "shortly"
                    ? getValue(shortlyForecast[curIndex].values, "REH")
                    : getAverage(weeklyForecast[curIndex].timeline, "REH")}
                  %
                </p>
              </li>
              <li>
                <i className="icon-pop"></i>
                <p>강수확률</p>
                <p>
                  {type === "shortly"
                    ? popFormat(getValue(shortlyForecast[curIndex].values, "POP"))
                    : popFormat(getAverage(weeklyForecast[curIndex].timeline, "POP"))}
                </p>
              </li>
              <li>
                <i className="icon-ra"></i>
                <p>
                  강수량
                  {type === "shortly"
                    ? snowFormat(getValue(shortlyForecast[curIndex].values, "SNO") !== "") && "(적설량)"
                    : snowFormat(getAverage(weeklyForecast[curIndex].timeline, "SNO") !== "") && "(적설량)"}
                </p>
                <p>
                  {type === "shortly"
                    ? pcpFormat(getValue(shortlyForecast[curIndex].values, "PCP")) +
                      snowFormat(getValue(shortlyForecast[curIndex].values, "SNO"))
                    : pcpFormat(getAverage(weeklyForecast[curIndex].timeline, "PCP")) +
                      snowFormat(getAverage(weeklyForecast[curIndex].timeline, "SNO"))}
                </p>
              </li>
              {type === "weekly" && (
                <>
                  <li>
                    <i className="icon-high"></i>
                    <p>최고기온</p>
                    <p>{getAverage(weeklyForecast[curIndex].timeline, "TMX")}&deg;</p>
                  </li>
                  <li>
                    <i className="icon-low"></i>
                    <p>최저기온</p>
                    <p>{getAverage(weeklyForecast[curIndex].timeline, "TMN")}&deg;</p>
                  </li>
                </>
              )}
            </ul>
          </section>
          <section className="range">
            {type === "shortly" ? (
              <div className="rangeList">
                <div className="pseudoDiv" style={{ left: (curIndex * 100) / 6 + "%" }}></div>
                {shortlyForecast.map((fcst, i) => (
                  <a href="#none" key={i} onClick={() => setCurIndex(i)}>
                    <i className={`icon-${shortlySkyState[i]}`}></i>
                    <p className="temp">{getValue(fcst.values, "TMP")}&deg;</p>
                    <p className="time">
                      {fcst.time.slice(0, 2)}
                      <span>시</span>
                    </p>
                  </a>
                ))}
              </div>
            ) : (
              <div className="rangeList">
                <div className="pseudoDiv" style={{ left: (curIndex * 100) / 6 + "%" }}></div>
                {weeklyForecast.slice(0, 6).map((fcst, i) => (
                  <a href="#none" key={i} onClick={() => setCurIndex(i)}>
                    <i className={`icon-${weeklySkyState[i]}`}></i>
                    <p className="temp">{getAverage(fcst.timeline, "TMP")}&deg;</p>
                    <p className="time">
                      {dateFormat(fcst.date)}
                      <span>{fcst.ampm}</span>
                    </p>
                  </a>
                ))}
              </div>
            )}
          </section>
        </>
      )}
    </>
  );
}

root.render(<App />);
